<template>
  <v-container fluid fill-height>
    <v-slide-y-transition mode="out-in">
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
          <v-card class="elevation-12">
            <v-toolbar>
              <v-toolbar-title class="green--text text--darken-3">Регістрація</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn flat round to="/signin" class="grey--text text--darken-2">
                Вхід
                <v-icon right class="green--text text--darken-3">arrow_forward</v-icon>
              </v-btn>
            </v-toolbar>
            <v-card-text>
              <v-form v-model="valid" lazy-validation ref="form">
                <v-text-field prepend-icon="email" color="green darken-3" name="email" label="E-mail" type="email" v-model="email" :rules="[rules.required, rules.email]" required></v-text-field>
                <v-text-field prepend-icon="lock" color="green darken-3" name="password" label="Пароль" type="password" v-model="password" :rules="[rules.required]" required></v-text-field>
                <v-text-field prepend-icon="lock" color="green darken-3" name="confirmPassword" label="Пароль" type="password" v-model="confirmPassword" :rules="[rules.required, rules.confirmPassword]" required></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="userSignUp" round class="green darken-3 ma-1 white--text" :disabled="!valid || isLoading || hasError" :loading="isLoading">Регістрація</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SignUp",
  data() {
    return {
      valid: true,
      email: "",
      password: "",
      confirmPassword: "",
      rules: {
        required: value => !!value || "Обов'язкове поле.",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Недійсна електронна пошта.";
        },
        confirmPassword: value =>
          value === this.password || "Повторіть ваш пароль."
      }
    };
  },
  computed: {
    ...mapGetters(["isLoading", "hasError"])
  },
  methods: {
    userSignUp() {
      if (this.$refs.form.validate()) {
        this.$store.dispatch("userSignUp", {
          email: this.email,
          password: this.password
        });
      }
    }
  }
};
</script>



